<template>
  <div class="tvprog">
    <!-- <h1> ### {{ chan.title }}</h1> -->
        <!-- <p v-show="(relevance != 'passed')" v-bind:class="(relevance == 'now') ? 'now' : ''">{{ readableDT(start) }} - {{ title }} --- {{ relevance }}</p> -->
        <!-- <p v-bind:class="(relevance == 'now') ? 'now' : ''">{{ readableDT(start) }} - {{ title }} --- {{ relevance }}</p> -->
        <p v-bind:class="(relevance == 'now') ? 'now' : ''">{{ readableDT(start) }} - {{ title }}</p>
        <!-- <p>{{ strToDate(prog.start) }}</p>
        <p>{{ strToDate(prog.stop) }}</p>
        <p>{{ new Date() }}</p> -->
  </div>
</template>

<script>
export default {
  name: 'TVProg',
  props: {
    title: String,
    start: Date,
    stop: Date
  },
  methods: {
      readableDT: function(date) {
            let hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
            let minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
            return hours + ':' + minutes;
        //   return date;
      }
  },
  computed: {
      relevance: function() {
        //   let startDate = this.strToDate(this.prog.start);
        //   let stopDate = this.strToDate(this.prog.stop);
          let currentDate = new Date();
            if (currentDate < this.start) {
                return 'later';
                }
            else if (currentDate <= this.stop) {
                return 'now';
                }
            else {
                return 'passed';
                }
      }
  }
}
</script>

