<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <button id="chstatebtn" @click="changeState($event)">{{ statesReadable[states[currentStateIndex]] }}</button>
    <tvchanswrapper/>

  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import TVChansWrapper from './components/TVChansWrapper.vue'

import Vue from 'vue'
export const eventEmitter = new Vue()

export default {
  name: 'App',
  components: {
    'tvchanswrapper': TVChansWrapper
  },
  data: function() {
      return {
          states: [
              'full',
              'now',
              'short',
              'long'
          ],
          statesReadable: {
              'full':'Все',
              'now':'Сейчас',
              'short':'Сейчас и ближайшие',
              'long':'Сейчас и все последующие'
              },
          currentStateIndex: 0
      }
  },
  methods: {
      changeState: function(e) {
        //   let currentBtnText = e.target.innerHTML;
        //   let newBtnTextIndex = Object.keys(this.statesReadable).indexOf(currentBtnText) + 1;
        //   let nextBtnText = Object.keys(this.statesReadable)[newBtnTextIndex];
        //   e.target.innerHTML = nextBtnText;
        
        this.currentStateIndex = (this.currentStateIndex + 1) > 3 ? 0 : this.currentStateIndex + 1;
        e.target.innerHTML = this.statesReadable[this.states[this.currentStateIndex]];
        eventEmitter.$emit('stateChanged', this.states[this.currentStateIndex])
      }
  }
}
</script>

