<template>
  <div class="tvchan">
    <!-- <h1> ### {{ chan.title }}</h1> -->
        <div class="chan-header">
            <img class="chan-logo" v-bind:src="chan.icon" alt="">
            <h3 class="chan-title">{{ chan.title }}</h3>
        </div>
        <ul>
            <li v-for="prog in progsToDisplay" v-bind:key="prog.title" >
                <tvprog 
                    :title="prog.title" 
                    :start="strToDate(prog.start)" 
                    :stop="strToDate(prog.stop)"/>
                <!-- <p>{{ readableDT(prog.start) }} --- {{ prog.title }}</p> -->
            </li>
        </ul>
        <hr>
  </div>
</template>

<script>

import { eventEmitter } from '../App.vue'

import TVProg from './TVProg.vue'

export default {
  name: 'TVChan',
  props: {
    chan: Object,
  },
  data: function() {
      return {
          currentState: 'full'
      }
  },
  created() {
      eventEmitter.$on('stateChanged', (newstate) => { this.currentState = newstate })
  },
  components: {
        'tvprog': TVProg
  },
  computed: {
    //   currentState: function () {
    //       let startDate = this.strToDate(this.prog.start);
    //       let stopDate = this.strToDate(this.prog.stop);
    //       let currentDate = new Date();
    //         if (currentDate < startDate) {
    //             return 'later';
    //             }
    //         else if (currentDate <= stopDate) {
    //             return 'now';
    //             }
    //         else {
    //             return 'passed';
    //             }
    //     },
        progsToDisplay: function() {
            if (this.currentState == 'full') return this.chan.progms;
            let result = [];
            for (let i = 0; i < this.chan.progms.length; i++) {
                const prog = this.chan.progms[i];
                let start = this.strToDate(prog.start);
                let stop = this.strToDate(prog.stop); //shit
                let currentDate = new Date();
                if (this.currentState == 'now' && (currentDate < start || currentDate > stop)) continue;
                if (stop < currentDate) continue;
                result.push(prog)
            }
            if (this.currentState == 'short') return result.slice(0,3);
            // result = this.chan.progms;
            // if (this.currentState == 'full') {
            //     result = [];
            // }
            return result;
        }
  },
  methods: {
      strToDate: function(strDate) {
          var date = new Date(
              strDate.substring(0,4), // year
              parseInt(strDate.substring(4,6))-1, // month (months starts with 0 index)
              strDate.substring(6,8), // day
              parseInt(strDate.substring(8,10))+2, // hour
              strDate.substring(10,12), // minute
              strDate.substring(12,14));// seconds
            return date;
      },

  }
}
</script>

